import React from "react";
import Subtitle from "../../components/Subtitle";

const intro = () => {
	return (
		<div class="px-5 2xl:px-36 xl:px-32 md:px-8 py-8 lg:py-16">
			<Subtitle text={"인사말"} />

			<div class="py-4 lg:py-8">
				<div class="text-sm leading-7 lg:text-xl lg:leading-10">
					우리 협회 홈페이지를 찾아주셔서 감사합니다.
				</div>
				<div class="text-sm leading-7 lg:text-xl lg:leading-10">
					우리 협회는 2000년 7월 1일 포항시 발달장애인의 권익옹호와 복지증진을
					위해 설립되었습니다. 저와 우리 협회 종사자들은 포항시 발달장애인의
					행복한 삶을 위하여 지역사회 안에서 함께 노력하며, 가족 이상의 힘이 될
					수 있는 협회가 되도록 노력하겠습니다. 여러분의 많은 관심과 사랑
					부탁드립니다.
				</div>
			</div>
		</div>
	);
};

export default intro;
